import { setStore, getStore } from '/utils/storage'

export default {
	['INIT_BUYCART'](state) {
		let initCart = getStore('buyCart');
		if(initCart) {
			state.cartList = JSON.parse(initCart);
		}
	},
	  // 
  	['ADD_CART'](state, { 
      productId, 
      productImg, 
      productName, 
      currCost,
      currPrice, 
      check = false, 
      total = 1, 
      ebuy,
      type}) {
    let cart = state.cartList; // 
    let falg = true;
    let goods = {
      productId,
      productImg,
      productName,
      currCost,
      currPrice,
      ebuy,
      type
    }
    if (cart.length) { // 
      cart.forEach(item => {
        if (item.productId === productId) {
          if (item.total >= 0) {
            falg = false;
            item.total += total;
          }
        }
      })
    }
    if (!cart.length || falg) {
      goods.total = total;
      goods.check = false;
      cart.push(goods);
      // console.log(goods)
    }
    state.cartList = cart;
    // localStorage
    setStore('buyCart', cart);
  },
   // 
  ['EDIT_CART'] (state, {productid, quality, check}) {
    let cart = state.cartList;

    if(productid) {
       cart.forEach((item) => {
        if(item.productId === productid) {
  
          item.total = quality;
          item.check = check;
        }
      })
    }
    // else if(productId) {//
    //   cart.forEach((item) => {
    //     if(item.productId === productId) {
    //       item.check = check;
    //     }
    //   })
    // }
     else { //
      cart.forEach((item) => {
        item.check = check ? true : false;
      })
    }
    state.cartList = cart;
    // localStorage
    setStore('buyCart', state.cartList);
    
  },
    //
  ['DELETE_CART'] (state, checkID) {
    let cart = state.cartList;
    checkID && checkID.forEach((id, idx) => {
       cart.forEach((item,i) => {
          if(item.productId === id) {
            cart.splice(i, 1);
          }
        })
    });
       
   state.cartList = cart;
    // localStorage
    setStore('buyCart', state.cartList);
  },
  //
  ['CHOOSE_ADDRESS'](state, {address,id,index}) {
    state.chooseAddress = address;
    state.addressId = id;
    state.addressIndex = index;
    
  },
  //
  ['CHOOSE_PROSORT'](state, {sortPro, index}) {
    state.sortProIndex = index;
    // state.sortProIndex2 = index2;
    state.sortPro = sortPro;
  },
  // id
  ['CHOOSE_LEVERONEID'](state, {oneId, twoId}) {
    state.oneId = oneId;
    state.twoId = twoId;
  },
  ['CHOOSE_LEVERONEID'](state, oneId) {
    state.oneId = oneId;
    // state.twoId = twoId;
  },
  ['CHOOSE_LEVERTWOID'](state, twoId) {
    // state.oneId = oneId;
    state.twoId = twoId;
  },
  //
  ['INIT_SORTLIST'](state, list) {
		 state.sortList = list;
	}
}


